@import url(https://fonts.googleapis.com/css?family=Varela);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@font-face {
	font-family: bit;
	src: url(/assets/bit.otf);
}
body {
	width: 100%;
	height: 100%;
	font-family: 'Varela', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #2b2b2b;
	padding-top: 55px;
	color: #ababab;
}
h1, h2, h3 {
	color: white;
}
.btn {
	border-radius: 0;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
	monospace;
}

hr {
	background-color: white;
}

.bg-dark {
	background-color: #202020 !important;
}

/* header */

.carousel-item {
	height: 45vh;
	min-height: 300px;
	background: no-repeat center center scroll;
	background-size: cover;
	text-align: center;
}
.slide {
	text-align: center;
	position: relative;
	top: 50%;
}
.slide-cap {
	-webkit-transform: translateY(-100%);
	        transform: translateY(-100%);
}
.carousel-button {
	-webkit-transform: translateY(-100%);
	        transform: translateY(-100%);
}
.account-name {
	text-align: center;
	font-family: bit;
	font-size: 80pt;
	margin-top: 20px;
	text-shadow: 5px 5px black;
}

/* end header */
/* content */

.container.content .row > div {
	background-clip: padding-box;
	padding: 20px;
}
.song-list {
	margin: 20px 0 0;
	padding: 0;
	list-style: none;
}
.song-list > li {
	width: 100%;
	min-height: 100px;
	background: #252525;
	margin-bottom: 20px;
}
.song-list > li .song-header {
	width: 100%;
	background: #ffffff45;
	padding: 20px;
	position: relative;
	overflow: hidden;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.song-list > li .song-header .song-title {
	font-size: 22px;
	color: white;
	display: inline-block;
	height: 40px;
	line-height: 40px;
	vertical-align: top;
	margin-left: 15px;
	letter-spacing: 0.5px;
	max-width: 90%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	position: relative;
	z-index: 5;
}
.song-list > li .song-header .btn.playback-btn {
	width: 40px;
	height: 40px;
	background: transparent;
	border-color: transparent;
	display: inline-block;
	vertical-align: top;
	position: relative;
	-webkit-transition: text-shadow 200ms;
	transition: text-shadow 200ms;
	z-index: 5;
}
.song-list > li .song-header .btn.playback-btn:active {
	text-shadow: 0 0 20px rgba(255,255,255,0.6);
}
.song-list > li .song-header .btn.playback-btn:focus {
	box-shadow: unset;
}
.song-list > li .song-header .btn.playback-btn:after {
	font-family: 'FontAwesome', sans-serif;
	font-size: 26px;
	display: block;
	width: 40px;
	height: 40px;
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
	line-height: 40px;
	text-shadow: 0 0 0 0.2rem rgba(248,249,250,.5);
	color: white;
}
.song-list > li .song-header .btn.playback-btn.pause-btn:after {
	content: "\F04C";
}
.song-list > li .song-header .btn.playback-btn.play-btn:after {
	content: "\F04B";
}
.song-list > li .song-header .buttons-container {
	list-style: none;
	margin: 0;
	padding: 0;
	display: inline-block;
	vertical-align: middle;
}
.song-list > li .song-header .buttons-container > * {
	width: 30px;
	height: 30px;
	line-height: 30px;
	position: relative;
	background: rgba(255,255,255,0.1);
	border: unset;
	margin-left: 5px;
}
.song-list > li .song-header .buttons-container > *:active {
	background: rgba(255,255,255,0.2);
}
.song-list > li .song-header .buttons-container > * > i {
	position: absolute;
	top: 0;
	left: 0;
	font-size: 12px;
	width: 30px;
	height: 30px;
	line-height: 30px;
	text-align: center;
}
.song-list > li .song-header .song-artist {
	color: #bfbfbf;
	text-align: right;
	text-decoration: none;
	-webkit-transition: color 200ms;
	transition: color 200ms;
	display: inline-block;
	vertical-align: middle;
	margin-left: 20px;
}
.song-list > li .song-header .song-artist:hover {
	color: white;
}
.song-list > li .song-header .song-artist .name {
	font-size: 16px;
}
.song-list > li .song-header .song-artist .username {
	font-size: 10px;
	margin-top: -5px;
}
.song-list > li .song-header .play-progress {
	position: absolute;
	bottom: 0;
	left: 0;
	max-width: 100%;
	height: 10px;
	background: #ffffff1a;
	z-index: 1;
}
.song-list > li .song-body {
	height: 56px;
	padding: 10px 20px;
	color: #757575;
	font-size: 12px;
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}
.song-list > li .song-body .song-rating {
	height: 100%;
	display: flex;
	align-items: center;
}
.song-list > li .song-body .song-rating .star {
	width: 30px;
	height: 30px;
	position: relative;
}
.song-list > li .song-body .song-rating.changeable .star {
	cursor: pointer;
}
.song-list > li .song-body .song-rating .star:before,
.song-list > li .song-body .song-rating .star:after {
	position: absolute;
	top: 0;
	left: 5px;
	width: 20px;
	height: 30px;
	line-height: 30px;
	text-align: left;
	font-size: 22px;
	font-family: FontAwesome, sans-serif;
	content: "\F005";
	color: #444444;
}
.song-list > li .song-body .song-rating .star.active:before {
	color: #d4b200;
}
.song-list > li .song-body .song-rating .star:after {
	color: transparent;
}
.song-list > li .song-body .song-rating .star.full:after,
.song-list > li .song-body .song-rating .star.half:after {
	color: #ffffff75;
}
.song-list > li .song-body .song-rating .star.half:after {
	content: "\F089";
}
.song-list > li .song-body .song-rating .star.hover:after {
	color: #dcdcdc;
	content: "\F005";
}
.song-list > li .song-body .song-dates {
	text-align: right;
}
.song-list > li .song-body .song-dates .song-creation-date,
.song-list > li .song-body .song-dates .song-update-date {
	display: block;
}

/* end content */
/* pages */

.form-signin {
	margin: auto;
	width: 400px;
}

/* end pages */

#root {
    height: 100%;
}

.App {
    height: 100%;
}

.editor {
    width: 80%;
    height: 100%;
    margin: auto;
}

.row {
    display: flex;
}

#instrEdit, .editor-header {
    background-color: #202020;
    border: 2px solid grey;
    box-shadow: 5px 5px black;
    color: white;
    padding: 10px;
}

.editor-header {
    margin: 15px -15px;
    padding: 20px;
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.header-left {
    display:flex;
    align-items: center;
}

.name-input {
    background: transparent;
    border: 0;
    border-bottom: 1px solid grey;
    margin: 0 20px;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
    font-size: 24px;
    text-align: center;
}

.name-input:focus {
    outline: none;
}

.header-controls {
    margin-left: 30px;
    display: inline;
}

#instrEdit {
    flex: 0.2 1;
}

input, select {
    background-color: #101010;
    color: white;
    margin-bottom: 10px;
    margin-top: 10px;
    margin-left: 5px;
    margin-right: 5px;
    border-color: grey;
}

#blockEdit {
    flex: 1 1;
    margin-left: 1em;
    box-shadow: 5px 5px #101010;
}

div.on, div.start, div.mid, div.end {
    background-color: #dee0e2;
}

div.on:hover, div.start:hover, div.end:hover {
    background-color: #a4a6aa;
}

div.off {
    background-color: #72777f;
}

div.off:hover {
    background-color: #dee0e2;
}

div.start, div.mid {
    background-color: #54D6AF;
}

div.end, div.mid {
    background-color: #D6547B;
}

.line {
    width: 100%;
    height: 65px;
    display: flex;
    flex-direction: row;
}

.tile {
    flex: 0 0 6.25%;
    border: solid 1px black;
}

